<template>
    <div>
        <headerNavBar :title='"配网"'></headerNavBar>
        <van-cell-group>
            <van-field
                v-model="wifiInfor.wifiName"
                placeholder="请输入wifi名称"
                input-align='center'
            />

            <van-field
                v-model="wifiInfor.wifiPassword"
                type="password"
                placeholder="请输入wifi密码"
                input-align='center'
            />
        </van-cell-group>
        <div class='submit'>
            <van-button class='submit_btn' plain round size="normal" @click='submit'>连接</van-button>
        </div>
        <van-popup v-model="AirkissOk" class='popup'>配网成功</van-popup>
    </div>
</template>

<script>
    import headerNavBar from '@/components/headerNavBar'
    export default {
        data(){
            return {
                wifiInfor:{
                    wifiName:'',
                    wifiPassword:''
                },
                AirkissOk:false
            }
        },
        methods:{
            onLoad(){
                //JsCallNative.callAirKissEncoder('ssid','password');
            },
            airkissCall(msg) {
                console.log(msg);
                this.AirkissOk = true;
                alert(msg);
            },
            submit(){
                if(this.wifiInfor.wifiName && this.wifiInfor.wifiName) {
                    android.showToast(this.wifiInfor.wifiName,this.wifiInfor.wifiName);
                }else {
                    alert('wifi名称和密码不能为空');
                }
            }
        },
        components:{
            headerNavBar:headerNavBar
        },
        created() {
            window.airkissCall = (msg) => {
                this.airkissCall(msg);
            }
        },
        mounted() {
            //window.airkissCall('hello');
        },
    }
</script>

<style scoped>
.popup {
    padding:20px 30px;
}
</style>


